* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

.preloader {
  margin-left: 48px;
  position: fixed;
  width: 100%;
  height: calc(100vh - 50px);
  background-color: hsl(230, 85%, 73%);
  display: flex;
  justify-content: center;
  align-items: center;
}
.preloader h1{
    color: white;
    position: absolute;
    top: calc(100vh - 25%);
    transform: translate(-(calc(100vh - 25%)));
}
.airplane{
    height: 200px;
    animation: airplane 1.5s ease infinite;
}

.cloud1,
.cloud2,
.cloud3,
.cloud4 {
  height: 100px;
  position: absolute;
  right: 0%;
  z-index: -1;
}

.cloud1 {
  top: 25%;
  transform: translate(100%, -25%);
  animation: clouds 3s ease infinite;
}
.cloud2 {
  top: 50%;
  transform: translate(100%, -50%);
  animation: clouds 4s ease infinite 2s;

}
.cloud3 {
  top: 75%;
  transform: translate(100%, -75%);
  animation: clouds 3s ease infinite 1.5s;
}
.cloud4 {
    top: 100%;
    transform: translate(100%, -100%);
    animation: clouds 2.5s ease infinite 1.5s;
  }
@keyframes airplane {
  from {
    transform: translateY(0px);
  }
  to {
    transform: translateY(50px);
  }
}

@keyframes clouds {
    from{
        left:100%;
    }
    to{
        left: -100%;
    }
}